body {
  background-color: rgba(210, 253, 239, 0.938);
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

img {
  height: 440px;
  width: 310px;
  border-radius: 10px;
  border: 4px solid rgba(0, 0, 0, 0.219);
  border-radius: 10px;
}

.center_box {
  height: 450px;
  width: 800px;
  background-color: rgb(18, 178, 184);
  border: 3.5px rgb(16, 158, 163) solid;
  box-shadow: 1px 2px 8px 5px rgb(178, 245, 222);
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 10px;
  padding: 10px;
}

.description {
  background-color: rgb(143, 221, 238);
  padding: 12px;
  margin: 20px;
  border: 4px solid #00000038;
  border-radius: 10px;
  height: 415px;
  padding: 15px;
}

h1,
h2,
h3,
h4,
h5 {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.link_box {
  margin: 10px;
}
.link_box a {
  color: black;
}

.project-box {
  margin: 10px;
}
.project-box a {
  color: black;
}
